import { h } from 'preact';
import { Link } from 'preact-router/match';
import { login, logout, isLoggedIn } from '../../utils/authservice';

const Header = () => (
	<nav class="navbar navbar-dark bg-dark">
		<a class="navbar-brand" href="#">Oynayalm</a>
	    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
			<span class="navbar-toggler-icon"></span>
	    </button>
	    <div class="collapse navbar-collapse" id="navbarText">
			<ul class="navbar-nav mr-auto">
				<li class="nav-item">
					<Link class="nav-link" activeClassName="active" href="/">Home</Link>
				</li>
			</ul>
			{
				isLoggedIn() ? (
					<button className="btn btn-danger log" onClick={() => logout()}>Log out </button>
				) : (
					<button className="btn btn-info log" onClick={() => login()}>Log In</button>
				)
			}
	    </div>
	</nav>
);

export default Header;



